import React from 'react';
import './App.css';
import { Home } from './screens/Home';
import { Layout } from './Layout';
import {Apply} from "./screens/Apply";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import { useLocalStorage } from './hooks/useLocalStorage';
import { fetchToken } from './auth/auth';
import { Provider } from 'use-http';
import {ToastProvider} from 'react-toast-notifications';

function App() {
  let [token, setToken] = useLocalStorage<string>("token","");

  const options = {
    interceptors:{
      request: async ({ options, url, path, route }) => {
          token = await fetchToken(process.env.REACT_APP_CLIENT_ID, process.env.REACT_APP_CLIENT_SECRET, "https://eu.battle.net/oauth/token");
          setToken(token)
        
        options.headers.Authorization = `Bearer ${token}`
        return options
      },
      // every time we make an http request, before getting the response back, this will run
      response: async ({ response }) => {
        const res = response
        return res
      }
    }
    }

  return (
  <Provider url="https://eu.api.blizzard.com" options={options}>
    <ToastProvider placement="bottom-center" autoDismiss={true}>
    <Router>
      <Switch>
        <Layout title="asd">
          <Route path="/home">
            <Home></Home>
          </Route>
          <Route path="/apply">
            <Apply></Apply>
          </Route>
          <Route path="/">
            <Redirect to="/home"/>
          </Route>
        </Layout>
      </Switch>
    </Router>
    </ToastProvider>
  </Provider>
  );
}


export default App;
